<template>
  <div class="home" ref="home">
    <!-- 头部搜索+轮播图 -->
    <div class="nav-box">
      <div class="nav-name">詠槑</div>
      <van-search
        v-model="value"
        shape="round"
        placeholder="搜索店内好物"
        @focus="goSearch"
      />
      <div class="banner-box">
        <van-swipe indicator-color="white" :autoplay="3000">
          <van-swipe-item v-for="(item, index) in BannerData" :key="index">
            <img
              class="auto-img"
              :src="item.image_src"
              @click="GoDetail(item.goods_id)"
            />
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <!-- 导航 -->
    <div class="list-box">
      <div class="list-item" v-for="(item, index) in listData" :key="index">
        <div class="img-box">
          <img class="auto-img" :src="item.image_src" alt="" />
        </div>
      </div>
    </div>
    <!-- 新鲜事 -->
    <div class="something-box" v-if="isImg">
      <div class="something-new">
        <span class="short-icon-left"></span>
        <span class="long-icon-left"></span> 新鲜事
        <span class="short-icon-right"></span>
        <span class="long-icon-right"></span>
      </div>
      <div class="video-box">
        <div v-if="!isPlay" class="video-img">
          <img class="auto-img" :src="getVideo.config1" alt="" />
          <div v-if="isImg" @click="clickPlayVideo" class="img-icon">
            <img class="auto-img" src="../assets/images/play.png" alt="" />
          </div>
        </div>
        <video ref="video" :src="getVideo.videoUrl" loop></video>
      </div>
    </div>
    <!-- 为你推荐 -->
    <div class="recommend">
      <div class="something-new" ref="scrollBox">
        <span class="short-icon-left"></span>
        <span class="long-icon-left"></span>为你推荐
        <span class="short-icon-right"></span>
        <span class="long-icon-right"></span>
      </div>
      <van-tabs title-active-color="#c43b31" title-inactive-color="#333333">
        <van-tab
          v-for="(item, index) in GoodsClassificationData"
          :title="item.cat_name"
          :key="index"
        >
          <!-- 分类图片 -->
          <div
            class="recommend-data-box"
            v-for="(item2, index2) in item.children"
            :key="index2"
          >
            <div class="recommend-title">{{ item2.cat_name }}</div>
            <div class="recommend-img-container clearfix">
              <div
                class="recommend-img-box fl"
                v-for="(item3, index3) in item2.children"
                :key="index3"
              >
                <img
                  class="auto-img"
                  :src="item3.cat_icon"
                  @click="goProductList(item3.cat_id)"
                />
                <div class="recommend-text">{{ item3.cat_name }}</div>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- 固定定位 -->
    <div class="home-fixed-box" v-if="isBox" @click="goScrollTop">⬆</div>
  </div>
</template>

<script>
import "../assets/less/home.less";
export default {
  name: "Home",

  data() {
    return {
      value: "",
      // 轮播图数据
      BannerData: [],
      // 列表数据
      listData: [],
      // 视频
      getVideo: {},
      // 判断是否播放
      isPlay: false,
      // 新鲜尝品轮播图
      newProjectData: [],
      //为你推荐
      recommendData: [],
      //商品数据
      GoodsClassificationData: [],
      // 商品子数组
      GoodsChildrenData: {},
      // 是否显示高亮
      isActive: 0,
      //是否显示盒子
      isBox: false,
      // 是否显示palyIcon
      isImg:false
    };
  },

  created() {
    // 获取轮播图数据
    this.getBannerData();
    // 获取导航数据
    this.getNavigationData();
    //获取新鲜尝鲜轮播图
    this.getVideoData();
    //获取商品数据
    this.getGoodsClassificationData();
  },

  mounted() {
    window.addEventListener("scroll", (e) => {
      // 兼容
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop >= 1200) {
        this.isBox = true;
      } else {
        this.isBox = false;
      }
    });
  },
  methods: {
    // 获取轮播图
    getBannerData() {
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/home/swiperdata",
      })
        .then((result) => {
          // 获取轮播图数据
          this.BannerData = result.data.message;
        })
    },
    // 获取导航数据
    getNavigationData() {
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/home/catitems",
      })
        .then((result) => {
          this.listData = result.data.message;
        })
    },
    // 点击播放视频
    clickPlayVideo() {
      if (!this.isPlay) {
        this.$refs.video.play();
        this.$refs.video.controls = true;
        this.isPlay = true;
      }
    },
    // 获取视频数据
    getVideoData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "POST",
        url: "/https://storeexpress.miniso.com/golbal/home/skuVideoDetail",
      })
        .then((result) => {
          this.getVideo = result.data.data.sku;
          this.$toast.clear();
          this.isImg = true;
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 获取商品分类数据
    getGoodsClassificationData() {
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/categories",
      })
        .then((result) => {
          
          result.data.message.map((v, i) => {
            this.GoodsClassificationData = Object.assign({}, result.data.message);
            // 删除对象属性
            this.$delete(this.GoodsClassificationData, "3");
            this.$delete(this.GoodsClassificationData, "29");
            this.$delete(this.GoodsClassificationData, "1");
          });
        })
    },
    //切换是否高亮
    toggleActive(index) {
      this.isActive = index;
    },
    // 页面滚动
    goScrollTop() {
      document.documentElement.scrollTop = this.$refs.scrollBox.offsetTop;
    },
    // 跳转商品列表
    goProductList(cid) {
      this.$router.push({ name: "ListSearch", params: { cid } });
    },
    // 商品详情
    GoDetail(goods_id) {
      this.$router.push({ name: "Detail", params: { goods_id } });
    },
    // 跳转搜索页面
    goSearch() {
      this.$router.push({ name: "Search" });
    },
  },
};
</script>
